:root {
  --padding: 10px;
  --color-text-header: white;
  --color-text-input: rgb(255, 255, 255);
  --color-input-border: rgb(190, 165, 184);
  --box-shadow: 20px 20px 20px 0px rgba(0, 0, 0, 0.75);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Source Sans Pro", "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
